import web3 from "../contracts/initWeb3";


/**
 * 小数点后18位
 * 将单位 wei 的数值转换为整数,并保留小数点后4位
 *  @param {string} value 
 */
const toolFromWei = (value: string | import("bn.js")) =>{
    const valueEther =  web3.utils.fromWei(value, 'ether');
    var re = /([0-9]+\.[0-9]{4})[0-9]*/;
    const valueNumber =  valueEther.replace(re,"$1");
    return valueNumber;
}


/**
 * 时间戳转换时间格式 2000-01-01 00:00:00
 * @param {number} timeStamp 时间戳
 */
const ConversionDate = (timeStamp:number) => {
    if(timeStamp>0){
        var date = new Date(timeStamp*1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        // var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        // var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        // return Y + M + D + h + m + s; //时分秒可以根据自己的需求加上
        return Y + M + D; //时分秒可以根据自己的需求加上
    }
    return '0000-00-00';
};

const ConversionDay = (timeStamp:number) =>{
    const day = timeStamp/86400;
    return day
}



export{
    toolFromWei,
    ConversionDate,
    ConversionDay
}

