import React from 'react';
import TextyAnim from 'rc-texty';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';


const AnimateTexty = styled(TextyAnim)`
    font-size: 50px;
    line-height: 60px;
    color: #FFFFFF;
    font-weight: 700;
    text-align: center;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        font-size: 26px;
        line-height: 40px;
    `};
   
`


// const text = 'This platform will blow your mind!'
// NFT-based mining.
// And  much more!
// Meet PokerFi!
// The leader in blockchain-based poker.
export default function AnimateText() {
    
    const { t  } = useTranslation();
    const getEnter = (e:any) => {
        switch (e.index) {
          case 0:
            return {
              rotate: 90,
              opacity: 0,
              y: -60,
            };
          case 10:
          case 1:
            return {
              y: -60,
              x: -10,
              opacity: 0,
            };
          case 9:
          case 2:
            return {
              y: -60,
              x: 20,
              opacity: 0,
            };
          case 3:
            return {
              y: 60,
              opacity: 0,
            };
          case 8:
          case 4:
            return {
              x: 30,
              opacity: 0,
            };
          case 5:
            return {
              enter: [
                {
                  scale: 2,
                  opacity: 0,
                  type: 'set',
                },
                { scale: 1.2, opacity: 1, duration: 300 },
                { scale: 0.9, duration: 200 },
                { scale: 1.05, duration: 150 },
                { scale: 1, duration: 100 },
              ],
              leave: {
                opacity: 0, scale: 0,
              },
            };
          case 11:
          case 6:
            return {
              scale: 0.8,
              x: 30,
              y: -10,
              opacity: 0,
            };
          case 12:
          case 7:
            return {
              scale: 0.8,
              x: 30,
              y: 10,
              opacity: 0,
            };
          default:
            return {
              opacity: 0,
            };
        }
    }
    return (
        <AnimateTexty
        enter={getEnter}
        leave={getEnter}
      >
        {t('home_text')}
      </AnimateTexty>
    )
}
