import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import AppBackgroundBody from '../../components/AppBackgroundBody';
import Particles from 'react-particles-js';
import logo_white_img from '../../assets/images/logo-white.png';
import { useViewport } from '../ViewportProvider';
import AnimateText from '../AnimateText';

const LogoContainer = styled.div`
    width: 1020px;
    max-width: 1020px;
    position: absolute;
    top: 150px;
    left:calc(50% - 510px);
    ${({ theme }) => theme.mediaWidth.upToLarge`
        width: 80%;
        left:10%;
    `};
`
const Logo = styled.div`
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    img{
        margin-left: 5%;
        margin-top: 50px;
    }
`

export default function HomeShow() {

    const { width } = useViewport();

    const [particles, setParticles] = useState<number>(10)

    useEffect(() => {
        if(width > 1200){
            setParticles(50)
        }
    }, [width])
    
    return (
        <AppBackgroundBody>
            <LogoContainer>
                <Particles
                    width={'100%'}
                    height={'100%'}
                    params={{
                        "particles": {
                            "number": {
                                "value": particles
                            },
                            "size": {
                                "value": 2
                            }
                        },
                        "interactivity": {
                            "events": {
                                "onhover": {
                                    "enable": true,
                                    "mode": "repulse"
                                }
                            }
                        }
                    }} />
                <Logo>
                    <img width={'90%'} src={logo_white_img} alt='Logo' />
                </Logo>
                <AnimateText />
            </LogoContainer>
        </AppBackgroundBody>
    )
}
