import React from 'react';
import styled from 'styled-components';
import HomeShow from '../../components/HomeShow';
import quote_background from "../../assets/images/quote-background.jpg";
import best_image from "../../assets/images/best-image.png";
import image_section from "../../assets/images/image-section.jpg";
import contact_hero from "../../assets/images/contact-hero.jpg";
import aim_image from "../../assets/images/aim.png";
import icon_user from "../../assets/images/icon-user.png";
import huanjing_image from "../../assets/images/huanjing.png";
import renwulingquchi_image from "../../assets/images/renwulingquchi.png";
import tansuo_image from "../../assets/images/tansuo.png";
import PKrongyubang_image from "../../assets/images/PKrongyubang.png";
import { useTranslation } from 'react-i18next';
import Reward from '../../components/Reward';

const HomeWrapper = styled.div`
    width: 100%;
`
const Twist = styled.div`
    width: 100%;
    padding:24px 20px;
    background-color: ${({theme}) => theme.bg2};
    color: ${({theme}) => theme.text1};
    text-align: center;
    font-size: 26px;
    font-weight: 100;
    b{
        font-weight: 700;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        font-size: 18px;
        line-height: 40px;
        padding: 10px 0px;
    `};
`
const InstructionsContainer =styled.div`
    width: 100%;
    background-color: ${({theme}) => theme.bg1};
    padding-top: 100px;
    padding-bottom: 50px;
    text-align: center;
    h2{
        color: ${({theme}) => theme.text2};
        margin-bottom: 30px;
        font-size: 40px;
        font-weight: 200;
        line-height: 53px;
        b{
            font-weight: 700;
        }
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding-top: 30px;
        padding-bottom: 30px;
        h2{
            margin-bottom: 20px;
            font-size: 24px;
            font-weight: 200;
            line-height: 40px;
            b{
                font-weight: 700;
            }
        }
    `};
`
const InstructionsText = styled.div`
    color: ${({theme}) => theme.text3};
    width: 50%;
    margin-left: 25%;
    margin-bottom: 30px;
    line-height: 26px;
    font-size: 16px;
    opacity: 0.8;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 80%;
        margin-left: 10%;
        font-size: 14px;
    `}
`
const Container = styled.section`
    width: 100%;
    padding: 120px 0px;
    background: url(${quote_background}) no-repeat;
    background-size: cover;
    background-position: center;
    color: ${({theme}) => theme.text1};
    font-size: 41px;
    font-weight: 200;
    line-height: 47px;
    p{
        max-width: 900px;
        text-align: center;
        margin: 0 auto;
        b{
            color: ${({theme}) => theme.text4}; 
            font-weight: 700 ;
        }
    }
    h6{
        text-align: center;
        font-size: 16px;
        margin-top: 30px;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding: 50px 0px;
        font-size: 20px;
        font-weight: 200;
        line-height: 47px;
        p{
            text-align: center;
            margin: 0 auto;
        }
    `}
`
const ContainerBest = styled.div`
    width: 100%;
    padding-top: 140px;
    padding-bottom: 100px;
    background-color: ${({theme}) => theme.bg3};
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding-top: 50px;
        padding-bottom: 50px;
    `}
`
const BestTitle = styled.div`
    width: 50%;
    margin-left: 25%;
    text-align: center;
    h3{
        color: ${({theme}) => theme.text1}; 
        margin-bottom: 30px;
        font-size: 40px;
        font-weight: 200;
        line-height: 53px;
        b{
            color: ${({theme}) => theme.text4}; 
            font-weight: 700 ;
        }
    }
    p{  
        color: ${({theme}) => theme.text5}; 
        font-size: 16px;
        font-weight: 200;
        line-height: 26px;

    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 90%;
        margin-left: 5%;
        h3{
            margin-bottom: 15px;
            font-size: 20px;
            font-weight: 200;
            line-height: 25px;
        }
        p{
            font-size: 14px;
            line-height: 24px;
        }
    `}
`

const BestFlex = styled.div`
    display: flex;
    flex-direction: row;
    padding-top: 70px;
    color: #fff;
    max-width: 960px;
    margin: 0 auto;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding:30px;
        flex-direction: row;
        flex-wrap:wrap;
    `}
`
const BestFlexLeft = styled.div`
    width: 33.33%;
    text-align: right;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 100%;
        text-align: left;
    `}
`
const BestFlexCenter = styled(BestFlexLeft)`
    text-align: center;
`
const BestFlexRight = styled(BestFlexLeft)`
    text-align: left;
`
const BestRow = styled.div`
    display: flex;
    flex-direction: row;
    justify-content:flex-end;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        justify-content:space-between; 
        flex-flow:row-reverse;
    `}
`
const BestRowRight =styled.div`
    display: flex;
    flex-direction: row;
    justify-content:flex-end;
`
const BestRowText = styled.div`
    padding: 20px;
    h4{
        color: ${({theme}) => theme.text6}; 
        font-size: 20px;
        line-height: 27px;
        font-weight: 400;
        margin-bottom: 3px;
    }
    p{
        color: ${({theme}) => theme.text5};
        font-size: 14px;
        line-height: 24px;
    }
`
const BestRowIcon = styled.div`
    padding-top: 24px;
`
const ContainerAlternative = styled(Container)`
    width: 100%;
    height: 450px;
    background: url(${image_section}) no-repeat;
    background-size: cover;
    background-position: center;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        height: 350px;
    `}
`
const AlternativeRow = styled.div`
    width: 60%;
    margin: 0 auto;
    text-align: left;
    h3{
        margin-top: 120px;
        font-size: 44px;
    }
    h5{
        font-size: 32px;
        font-weight: 200;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    button{
        background-color: ${({theme}) => theme.primary1};
        color: ${({theme}) => theme.text1};
        font-size: 14px;
        padding:15px 50px;
        cursor: pointer;
        &:hover {
            background-color: ${({ theme }) => theme.text2};
        }
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 90%;
        h3{
            margin-top: 40px;
            font-size: 22px;
        }
        h5{
            font-size: 18px;
            font-weight: 200;
            margin-top: 20px;
            margin-bottom: 20px;
        }
        button{
            padding:10px 30px;
        }
    `}
`

const ContainerConnect = styled.div`
    width: 100%;
    height: 940px;
    background: url(${contact_hero}) no-repeat;
    background-size: cover;
    background-position: center;
    color:#fff;
    font-size: 1.4rem;
    text-align: center;
    p{
        width: 50%;
        margin: 20px auto;
        line-height: 25px;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        p{
            width: 90%;
            margin: 20px auto;
            line-height: 25px;
        }
    `}
    
`

const ConnectTitle = styled.div`
    color: ${({theme}) => theme.text1}; 
    font-size: 40px;
    line-height: 53px;
    text-align: center;
    padding-top: 150px;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        padding-top: 50px;
    `}
`

const ContainerInfoStyle = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 50%;
    margin: 0 auto;
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 90%;
    `}
`
const ContainerInfo = styled.div`
    width: 40%;
    margin-left: 4%;
    background-color: rgba(255, 255, 255, 0.07);
    padding: 15px 20px 30px;
    text-align: left;
    h3{
        padding-top: 15px;
        padding-bottom: 10px;
    }
    p{
        width: auto;
        margin: 0;
        line-height: 30px;
        opacity: 0.6;
    }
    ${({ theme }) => theme.mediaWidth.upToMedium`
        width: 100%;
    `}
    button{
        outline: none;
        margin-top: 20px;
        padding: 10px 54px;
        border: 2px solid #ffffff;
        border-radius: 3px;
        font-size: 17px;
        font-weight: 600;
        color: #ffffff;
        background-color: rgba(127,127,127,0);
        cursor: pointer;
        &:hover{
            background-color: #7100B8;
            border: 2px solid #6f42c1;
        }
    }
`
const ContactForm = styled.div`
    display: flex;
    flex-direction: column;
    label{
        padding-top: 10px;
        padding-bottom: 10px;
    }
    input,textarea{
        outline: none;
        width: 90%;
        padding: 8px 11px 6px;
        color: #ccd7e0;
        font-size: 17px;
        font-weight: 300;
        background: transparent;
        border: none;
        background-color: rgb(142,151,161,0.13);
        border: 1px solid rgb(167,186,196,0.75);
    }
    
`


export default function Home() {
    
    const { t  } = useTranslation();
    // {t('homeScreen')}
    return (
        <HomeWrapper>
            <HomeShow />
            <Twist>{t('We_are_here_to')} <b>{t('Revolutionize_the_World')}</b></Twist>
            <InstructionsContainer>
                <h2><b>{t('Vision')}</b> {t('of_PokerFi_Market')}</h2>
                <InstructionsText>
                {t('PokerFi_introduce')}
                </InstructionsText>
            </InstructionsContainer>
            
            <Container>
                <p>{t('your_Poker_introduce_1')}<b>{t('your_Poker_introduce_2')}</b>.</p>
                <h6>{t('your_Poker_name')}</h6>
            </Container>
            <Reward />
            <ContainerBest>
                <BestTitle>
                    <h3>{t('What_We_do')}<b>{t('We_do_Best')}</b></h3>
                    <p>{t('We_do_PokerFi')}</p>
                </BestTitle>
                <BestFlex> 
                    <BestFlexLeft>
                        <BestRow>
                            <BestRowText>
                                <h4>{t('Our_Goal')}</h4>
                                <p>{t('Our_Goal_introduce')}</p>
                            </BestRowText>
                            <BestRowIcon>
                                <img width={'40px'}  src={aim_image} alt='Our Goal' />
                            </BestRowIcon>
                        </BestRow>
                        <BestRow>
                            <BestRowText>
                                <h4>{t('Who_are_We')}</h4>
                                <p>{t('Who_are_We_introduce')}</p>
                            </BestRowText>
                            <BestRowIcon>
                                <img width={'40px'}  src={icon_user} alt='Who are We' />
                            </BestRowIcon>
                        </BestRow>
                        <BestRow>
                            <BestRowText>
                                <h4>{t('Environmental_Impact')}</h4>
                                <p>{t('Environmental_Impact_introduce')}</p>
                            </BestRowText>
                            <BestRowIcon>
                                <img width={'40px'}  src={huanjing_image} alt='Environmental Impact' />
                            </BestRowIcon>
                        </BestRow>
                    </BestFlexLeft>
                    <BestFlexCenter>
                        <img width={'76%'}  src={best_image} alt='best' />
                    </BestFlexCenter>
                    <BestFlexRight>
                        <BestRowRight>
                            <BestRowIcon>
                                <img width={'40px'}  src={renwulingquchi_image} alt='Principle' />
                            </BestRowIcon>
                            <BestRowText>
                                <h4>{t('Principle')}</h4>
                                <p>{t('Principle_introduce')}</p>
                            </BestRowText>
                        </BestRowRight>
                        <BestRowRight>
                            <BestRowIcon>
                                <img width={'40px'}  src={tansuo_image} alt='Mining for Everyone' />
                            </BestRowIcon>
                            <BestRowText>
                                <h4>{t('Mining_for_Everyone')}</h4>
                                <p>{t('Mining_for_Everyone_introduce')}</p>
                            </BestRowText>
                        </BestRowRight>
                        <BestRowRight>
                            <BestRowIcon>
                                <img width={'40px'}  src={PKrongyubang_image} alt='Our Token' />
                            </BestRowIcon>
                            <BestRowText>
                                <h4>{t('Our_Token')}</h4>
                                <p>{t('Our_Token_introduce')}</p>
                            </BestRowText>
                        </BestRowRight>
                    </BestFlexRight>
                </BestFlex>
            </ContainerBest>
            <ContainerAlternative>
                <AlternativeRow>
                    <h3>{t('Alternative_Form_of_Mining')}</h3>
                    <h5>{t('Acquire_PokerFi_Today')}</h5>
                    <button>{t('Get_it_Now')}</button>
                </AlternativeRow>
            </ContainerAlternative>
            <ContainerConnect>
                <ConnectTitle>{t('Let')} <b>{t('Connect')}</b></ConnectTitle>
                <p>{t('Connect_introduce')}</p>
                <ContainerInfoStyle>
                    <ContainerInfo>
                        <h3>{t('Our_Contact_Info')}</h3>
                        <p>{t('Email')}</p>
                        <p>{t('Street')}</p>
                        <p>{t('Address')}</p>
                        <h3>{t('Connect_with_us')}</h3>
                        <p>{t('Building')}</p>
                    </ContainerInfo>
                    <ContainerInfo>
                        <h3>{t('Send_us_a_Message')}</h3>
                        <ContactForm>
                            <label>{t('Name')}</label>
                            <input type="text" />
                        </ContactForm>
                        <ContactForm>
                            <label>{t('Email_Name')}</label>
                            <input type="text" />
                        </ContactForm>
                        <ContactForm>
                            <label>{t('Message')}</label>
                            <textarea name="Message" id="Message" required></textarea>
                        </ContactForm>
                        <button>{t('Send_now')}</button>
                    </ContainerInfo>
                </ContainerInfoStyle>
            </ContainerConnect> 
        </HomeWrapper>
    )
}
