import React from 'react';
import styled from 'styled-components';
import hero_background from "../../assets/images/hero-background.jpg";

const BackgroundBodyWapper = styled.div`
    position: relative;
    width: 100%;
    min-height: 100vh;
    background: url(${hero_background}) no-repeat;
    background-size: cover;
    background-position: center;
`
export const GradientBack = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg,rgba(0,242,255,0.8) 0%, rgba(113,0,184,0.8) 100%);
`

export default function AppBackgroundBody({ children }: { children: React.ReactNode }) {
    return (
        <BackgroundBodyWapper>
            <GradientBack />
            {children}
        </BackgroundBodyWapper>
    )
}
