import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import jiaoyi from "../../assets/images/jiaoyi.png";
import fafang from "../../assets/images/fafang.png";
import lingquguanggao from "../../assets/images/lingquguanggao.png";
import { useWallet } from 'use-wallet';
import web3 from '../../contracts/initWeb3';
import { ContractReward } from '../../contracts';
import { toolFromWei } from '../../utils/tool';

const RewardWrapper = styled.div`
    width: 100%;
    background-color: #fff;
    box-sizing: border-box;
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
`
const Title = styled.h2`
    color: ${({theme}) => theme.text2};
    margin-bottom: 30px;
    font-size: 40px;
    font-weight: 200;
    line-height: 53px;
    b{
        font-weight: 700;
    }

`
const RewardStyles = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
`

const RowStyles = styled.div`
    width: 300px;
    color: ${({theme}) => theme.text3};
    line-height: 26px;
    font-size: 16px;
    box-sizing: border-box;
    text-align: center;
    img{
        margin: 10px;
    }
`
const GetButton = styled.button`
    background-color: ${({theme}) => theme.primary1};
    color: ${({theme}) => theme.text1};
    font-size: 14px;
    padding:15px 50px;
    cursor: pointer;
    margin-top: 50px;
    &:hover {
        background-color: ${({ theme }) => theme.text2};
    }
`
declare const window: any;
export default function Reward() {

    const wallet = useWallet();
    const activate = (connector: "injected" | "walletconnect" ) => wallet.connect(connector);
    const [myAddress, setMyAddress] = useState('')
    const [buttonType, setButtonType] = useState(false);
    const [totalSales, setTotalSales] = useState('0')
    const [totalReleased, setTotalReleased] = useState('0')
    const [releasable, setReleasable] = useState('链接钱包，查看我可以领取的奖励');


    useEffect(() => {
        getAppInfo();
        getTotalShares();
        if(myAddress){
            getReleasable();
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [myAddress])

    //获取地址
    const getAppInfo = async () => {
        try {
            const address = await web3.eth.getAccounts();
            console.log(address)
            if(address.length > 0){
                setMyAddress(address[0]);
                setButtonType(true);
            }else{
                setButtonType(false);
            } 
        } catch (error) {
            console.log(error)
        }
    }

    //交易总量 y已经发放奖励
    const getTotalShares = async () =>{
        const data = await ContractReward.methods.totalShares().call();
        const data_1 = await ContractReward.methods.totalReleased().call();
        setTotalSales(toolFromWei(data))
        setTotalReleased(toolFromWei(data_1))
    }

    //可领取的奖励
    const getReleasable = async () =>{
        const data = await ContractReward.methods.releasable(myAddress).call();
        setReleasable(toolFromWei(data))
    }

    //领取奖励
    const releaseClick = async () => {
        if(Number(releasable) === 0){
            alert('没有可领取的奖励！');
            return;
        };
        ContractReward.methods.release()
        .send({from:myAddress})
        .on('transactionHash',function(hash: any){
            console.log(hash)
        })
        .on('receipt',function(receipt: any){
            getReleasable();
            alert("领取成功");
        })
        .on('error',function(error: any,receipt: any){
            console.log(receipt)
        })
    }

    const injectedClick = async () =>{
        try {
            const chainId = window.ethereum.networkVersion;
            // eslint-disable-next-line eqeqeq
            if(chainId == wallet.chainId){
                activate('injected')
            }else{
                alert("网络错误,请将网络设置为：Binance Smart Chain!");
            }
        } catch (error) {
            alert("网络错误,请安装MetaMask或在Dapp钱包中打开！");
        }
    };

    try {
        window.ethereum.on('accountsChanged', function (accounts: any[]) {
            if(accounts.length > 0){
                setMyAddress(accounts[0]);
            }
        });
    } catch (error) {
        console.log(error)
    }


    return (
        <RewardWrapper>
            <Title><b>PokerFi</b>的奖励</Title>
            <RewardStyles>
                <RowStyles>
                    <img width={'20px'}  src={jiaoyi} alt='Mining for Everyone' />
                    <span>交易总量：{totalSales}</span>
                </RowStyles>
                <RowStyles>
                    <img width={'20px'}  src={fafang} alt='Mining for Everyone' />
                    <span>已发放奖励：{totalReleased}</span>
                </RowStyles>
                <RowStyles>
                    <img width={'20px'}  src={lingquguanggao} alt='Mining for Everyone' />
                    <span>可领取奖励：{releasable}</span>
                </RowStyles>
            </RewardStyles>
            {
                buttonType ? <GetButton onClick={releaseClick}>领取奖励</GetButton> : <GetButton onClick={injectedClick}>链接钱包，查看奖励</GetButton>
            }
        </RewardWrapper>
    )
}
